/* Design inspiration:  https://dribbble.com/shots/1978243-Latest-News */
body, html {
  font-family: 'Montserrat', sans-serif;
  background-color: #ffffff; }

.card {
  width: 280px;
  display: block;
  margin: 30px;
  -webkit-transition: .25s;
  transition: .25s; }
  .card:hover {
    cursor: pointer; }
    .card:hover .button-primary {
      -webkit-transform: translate(10px, 0);
              transform: translate(10px, 0); }

.card-header {
  height: 150px;
  width: 100%;
  padding: 15px;
  width: 100%;
  background-size: cover;
  color: #fff;
  border-radius: 15px 15px 0px 0px;
  border-width: 1px 1px 0px 1px;
  border-style: solid;
  border-color: darkolivegreen; }

.card-header--title {
  text-transform: uppercase;
  margin: 0; }

.card-body {
  padding: 15px;
  background-color: #fff;
  height: 200px;
  width: 100%;
  border-radius: 0px 0px 15px 15px;
  border-width: 0px 1px 1px 1px;
  border-style: solid;
  border-color: darkolivegreen; }

.date {
  font-size: 11px;
  font-weight: 600;
  color: grey; }

.body-content {
  padding-bottom: 40px;
  font-size: 13px;
  line-height: 1.8; }

.button-primary {
  border: none;
  box-shadow: none;
  font-family: inherit;
  background-color: transparent;
  color: #ffffff;
  font-size: 15px;
  -webkit-transition: .25s;
  transition: .25s; }
  .button-primary i {
    padding-right: 4px; }

.designer-link {
  color: #fff;
  text-align: center;
  fon-size: 12px;
  text-decoration: none;
  display: block; }

#flex-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 55px;
  width: 75%; }

#flex-container > .flex-item {
  flex: auto; }

#flex-container > .raw-item {
  width: 5rem; }

#flex-container > div {
  border: 1px solid #f00;
  padding: 1rem; }

#flex-container > .raw-item {
  border: 1px solid #000; }

.row {
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
